/* General styling */
html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
}

h1 {
  page-break-before: left;
}

h2 {
  page-break-before: right;
}

img {
  max-width: 100%;
  height: auto;
}

.chapter-introduction {
  page: intro;
}

#cover {
  page: toc;
  list-style: none;
}

@page toc {
  --base-color: green;
}
.pagedjs_toc_page + .pagedjs_blank_page {
  --base-color: green;
  outline: 3px dashed green;
}

@page :nth(1) {
  --base-color: green;
}
@page :first {
  --base-color: green;
}
@page intro {
  --base-color: brown;
}
.pagedjs_intro_page + .pagedjs_blank_page {
  outline: 4px dotted pink;
  --base-color: brown;
}

.pagedjs_intro_page ul li a {
  outline: 1px dotted red;
  color: greenyellow;
}

#cover {
  page-break-after: always;
}

span.footnote {
  float: footnote;
}

/* For placement op p5js sketch relative to the page */
.pagedjs_page {
  position: relative;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

